import { css } from '@emotion/react';

import { AnimatedTransition } from 'src/components/animated';
import { Typography } from 'src/components/dataDisplay';
import { Stack } from 'src/components/layout';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

const contentLayout = css`
  text-align: center;
`;

const optionLayout = css`
  height: 160px;
  padding-top: 20px;
  padding-bottom: 20px;

  border: 1px solid #ffffff;
  border-radius: 8px;

  color: #ffffff;
  background: transparent;

  width: 100%;

  overflow: hidden;
  word-break: keep-all;
  overflow-wrap: break-word;
`;

interface BestFitProductQuestionProps {
  index: number;
  onLeftClick: () => void;
  onRightClick: () => void;
}

const BestFitProductQuestion = ({
  index,
  onLeftClick,
  onRightClick,
}: BestFitProductQuestionProps): JSX.Element | null => {
  const { name: brandName } = useProductInfo();
  const { surveys } = useSurveysForBestFit({
    brandName,
  });

  const { question = '', options = '' } = surveys?.at(index) ?? {};
  const questions = question.split('\n');

  const [leftOption, rightOption] = options;
  const leftOptions = leftOption.split('\n');
  const rightOptions = rightOption.split('\n');

  if (!surveys || index < 0 || index > surveys.length) {
    return null;
  }

  console.log('BestFitProductQuestion', 'index', index);
  return (
    <AnimatedTransition>
      <Stack spacing={40} justifyContent="center" css={contentLayout}>
        <Typography variant="h3">
          {questions.map((text, index) => {
            const key = `${index}-${text}`;
            return <div key={key}>{text}</div>;
          })}
        </Typography>
        <Stack direction="row" spacing={20} justifyContent="space-between">
          <button type="button" css={optionLayout} onClick={onLeftClick}>
            {leftOptions.map((text, lineIndex) => {
              const lineKey = `${lineIndex}-${text}`;
              return (
                <span key={lineKey}>
                  {text}
                  <br />
                </span>
              );
            })}
          </button>
          <button type="button" css={optionLayout} onClick={onRightClick}>
            {rightOptions.map((text, lineIndex) => {
              const lineKey = `${lineIndex}-${text}`;
              return (
                <span key={lineKey}>
                  {text}
                  <br />
                </span>
              );
            })}
          </button>
        </Stack>
      </Stack>
    </AnimatedTransition>
  );
};

export default BestFitProductQuestion;
