import type { UseSpringProps } from '@react-spring/web';
import { easings } from '@react-spring/web';

export const BOTTOM_TO_UP = {
  from: {
    opacity: 0,
    y: 100,
  },
  to: {
    opacity: 1,
    y: 0,
  },
};

export const LEFT_TO_RIGHT = {
  from: {
    opacity: 0,
    x: -100,
  },
  to: {
    opacity: 1,
    x: 0,
  },
};

export const EASE_IN_OUT_BACK_CONFIG = {
  easing: easings.easeInOutBack,
};
export const EASE_OUT_ELASTIC_CONFIG = {
  easing: easings.easeOutElastic,
};

export const DEFAULT_OPTIONS: UseSpringProps = {
  ...BOTTOM_TO_UP,
  config: EASE_IN_OUT_BACK_CONFIG,
};

export const LEFT_TO_RIGHT_OPTIONS: UseSpringProps = {
  ...LEFT_TO_RIGHT,
  config: EASE_IN_OUT_BACK_CONFIG,
};

export const LEFT_TO_RIGHT_ELASTIC_OPTIONS: UseSpringProps = {
  ...LEFT_TO_RIGHT,
  config: EASE_OUT_ELASTIC_CONFIG,
};

export const REPEAT_OPTIONS: UseSpringProps = {
  loop: true,
  from: { x: -20, opacity: 0.5 },
  to: [
    { x: 0, opacity: 1 },
    { x: 20, opacity: 0.5 },
    { x: 0, opacity: 1 },
    { x: -20, opacity: 0.5 },
  ],
  config: EASE_IN_OUT_BACK_CONFIG,
};
