import { css } from '@emotion/react';

// 사용성이 떨어진다. Stack 의 경우 직접 fullWidth 가 포함되었다.
const wrapper = css`
  flex: 0 0 100%;
`;

interface FullWidthBoxProps {
  children: React.ReactNode;
}
const FullWidthBox = ({
  children,
  ...others
}: FullWidthBoxProps): JSX.Element => {
  return (
    <div css={wrapper} {...others}>
      {children}
    </div>
  );
};

export default FullWidthBox;
