import { css } from '@emotion/react';
import { useEffect, useState } from 'react';

const sizedWrapper = (width: number) => css`
  width: ${width}px;
`;

interface VerticalScrollableBoxProps {
  children: React.ReactNode;
}

// 최소한의 반응형 처리 - width 가 height 보다 클때 height 를 width 로 사용하도록 처리합니다.
// 가장 상단에 사용하는 것을 추천합니다.. 예, pages/.+/index.tsx
const ThinRectangleBox = ({
  children,
  ...others
}: VerticalScrollableBoxProps): JSX.Element => {
  const [isWide, setIsWide] = useState(false);
  const [width, setWidth] = useState<number | null>(null);

  const onResize = () => {
    const isWidthBigger =
      window.innerWidth > 0 && window.innerWidth > window.innerHeight;
    setIsWide(isWidthBigger);
    setWidth(isWidthBigger ? window.innerHeight : null);
  };
  useEffect(() => {
    window.addEventListener('resize', onResize);
    onResize();
    return () => {
      window.removeEventListener('resize', onResize);
    };
  }, [onResize]);

  return (
    <div css={isWide && width && sizedWrapper(width)} {...others}>
      {children}
    </div>
  );
};

export default ThinRectangleBox;
