import { css } from '@emotion/react';

const progressBarLayout = css`
  width: calc(100% - 2px);
  height: 8px;
  border: 1px solid #ffffff;
  border-radius: 100px;
`;

const progressBarStyle = (percent: number) => css`
  width: ${percent}%;
  height: 8px;
  background: #ffffff;
`;

interface ProgressBarProps {
  percent: number;
}
const ProgressBar = ({ percent }: ProgressBarProps): JSX.Element => {
  return (
    <div css={progressBarLayout}>
      <div css={progressBarStyle(percent)} />
    </div>
  );
};

export default ProgressBar;
